h1,
h1 + article section.bg-lt-gray p,
h2,
h2 + article section.bg-lt-gray p {
  line-height: 1.4 !important;
}

.attribute {
  color: #2196f3;
}

.box {
  border: 0.0625rem solid #000;
  margin: 1rem;
  padding: 0;
}

.box p,
.box span {
  background-color: #2196f3;
  color: #fff;
  text-align: center;
}

.box p:nth-child(even),
.box span:nth-child(even) {
  background-color: #4caf50;
}

.box p[class*=pad],
.box span[class*=pad] {
  display: inline-block;
  margin: 0 1rem;
}

.box p.pos-a,
.box span.pos-a {
  left: 15rem;
}

.box p.pos-s,
.box span.pos-s {
  top: 0;
  z-index: 1000;
}

.box p.pos-s + p,
.box span.pos-s + p {
  padding-bottom: 62.5rem;
}

.box.col {
  height: 25rem;
}

.box.col * {
  width: 100%;
}

.flexbox {
  align-items: flex-start;
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
}

.flexbox ul {
  border: 0.0625rem solid #000;
  margin: 0.375rem;
}

.flexbox ul[class*=col] {
  height: 15.625rem;
  width: 9.375rem;
}

.flexbox ul[class*=col][class*=wrap] {
  width: 18.75rem;
}

.flexbox ul.col-full {
  height: 18.75rem;
}

.flexbox ul[class*=row] {
  height: 9.375rem;
}

.flexbox ul[class*=row][class*=wrap] {
  height: 18.75rem;
}

.flexbox ul.row {
  width: 18.75rem;
}

.flexbox ul li {
  align-items: center;
  background-color: #2196f3;
  color: #fff;
  display: flex;
  justify-content: center;
  min-height: 6.25rem;
  min-width: 7.5rem;
}

.flexbox ul li:nth-child(even) {
  background-color: #4caf50;
  min-height: 4.6875rem;
  min-width: 6.25rem;
}

.grid {
  border: 0.0625rem solid #000;
}

.grid:not(:last-of-type) {
  margin-bottom: 1rem;
}

.grid.h-50 {
  height: 3.125rem;
}

.grid li {
  background-color: #2196f3;
  color: #fff;
}

.grid li:nth-child(even) {
  background-color: #4caf50;
}

.main {
  padding-bottom: 2rem;
  padding-left: 13.75rem;
  padding-right: 1.5rem;
  padding-top: 9.75rem;
}

.main code {
  color: #9c27b0;
}

.pos-f-l.nav-v {
  left: 1rem;
  top: 10.25rem;
  width: 11.25rem;
}

.tag {
  color: #ba000d;
  font-weight: normal;
}

.value {
  color: #4caf50;
}
